.container {
    width: 700px;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px dashed #fff;
    border-radius: 5px;
    transition: all ease 0.3s;
}

.container img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 5px;
}

.container.active {
    border: 1px solid #fff;
}

.icon {
    font-size: 120px;
    color: #fff;
}

header {
    color: #160040;
    font-size: 35px;
    padding: 10px;
    text-align: center;
}

span {
    color: #fff;
    padding: 5px;
    font-size: 25px;
    font-weight: 400;
}

button {
    padding: 5px;
    border: none;
    outline: none;
    border-radius: 5px;
    width: 140px;
    height: 45px;
    cursor: pointer;
    box-shadow: -2px 7px 16px -3px rgba(154, 6, 128, 1);
    font-weight: 400;
    font-size: 15px;
}

button:focus {
    outline: none;
}

@media screen and (max-width: 750px) {
    .container {
        width: 90vw;
        height: 400px;
    }
    .icon {
        font-size: 80px;
    }
    header {
        font-size: 25px;
    }
}